.App {
  display: flex;
  justify-content: space-between;
  height: 100vh;
  width: 100vw;
  background-image: url(./images/poker-background.jpeg);
  background-size: cover;
  background-position: center;
}

.buttons {
  padding-top: calc(100vh / 2.2);
  text-align: center;
}

.card-container {
  margin-top: 18px;
}

.ml {
  margin-left: 26px;
}

.mr {
  margin-right: 38px;
}

.winner-title {
  height: fit-content;
  margin-top: 36px;
  padding: 0 12px;

  font-size: 48px;
  font-weight: bold;
  letter-spacing: 12px;
  color: white;

  border-style: solid;
  border-radius: 20px;

  text-shadow: 
  0 1px 0 #ccc, 
  0 2px 0 #c9c9c9, 
  0 3px 0 #bbb, 
  0 4px 0 #b9b9b9, 
  0 5px 0 #aaa, 
  0 6px 1px rgba(0,0,0,.1), 
  0 0 5px rgba(0,0,0,.1), 
  0 1px 3px rgba(0,0,0,.3), 
  0 3px 5px rgba(0,0,0,.2), 
  0 5px 10px rgba(0,0,0,.25), 
  0 10px 10px rgba(0,0,0,.2), 
  0 20px 20px rgba(0,0,0,.15);

  box-shadow:
  0 1px 0 #ccc, 
  0 2px 0 #c9c9c9, 
  0 3px 0 #bbb, 
  0 4px 0 #b9b9b9, 
  0 5px 0 #aaa, 
  0 6px 1px rgba(0,0,0,.1), 
  0 0 5px rgba(0,0,0,.1), 
  0 1px 3px rgba(0,0,0,.3), 
  0 3px 5px rgba(0,0,0,.2), 
  0 5px 10px rgba(0,0,0,.25), 
  0 10px 10px rgba(0,0,0,.2), 
  0 20px 20px rgba(0,0,0,.15);
}
